	import {
			mapState,
			mapMutations
		} from 'vuex';
	import apiName from '../../../static/commonjs/request/apiName.js'
    export default {
    	data() {
    		return {
    			is_not:false,
				msgList:[],
    		}
    	},
        created(){
        	// this.collect()
        },
        onLoad() {
			this.messageClick()
        },
        computed: {
        	...mapState(['token','presentAddress','jobCategory','indexSearchJob'])
        },
        methods: {
			messageClick(){
				var _this = this;
				_this.$http.post(apiName.MSGLIST,{
					params:{
						token:_this.token,
						// token:'soz55e26558',
						page:1,
						page_size:50
					}
				}).then(res => {
					if(res.data.data){
						console.log(res);
						_this.msgList = res.data.data.msg_list;
						if(_this.msgList.length <= 0){
							_this.is_not = true;
						}
					}
				}).catch(err => {
					console.log(err);
				})
			}
        }
    }
